 body {
     background-color: #f3f3f3;
 }

 .container {
     display: flex;
     justify-content: center;
     align-items: center;
     flex-direction: column;
 }

 .image{
     width: 370px;
     margin-top: 50px;
 }
 
 @media (max-width: 770px) {
     .container{
         margin: 0 10%;
     }
     .image{
         width: 100%;
     }
 }